var bizagi = bizagi || {};
var isDesktopDevice = bizagi.detectDevice() === "desktop";

bizagi.modalError = (function () {

    this.setDefaultParams = function (error, titleKeyResource, showGenericMessage) {

        var params = {
            titleModal: "Error",
            showGenericMessage: (typeof showGenericMessage === 'undefined') ? true : showGenericMessage
        };

        titleKeyResource = titleKeyResource || "workportal-general-error-page-title";

        if (bizagi.localization.getResource(titleKeyResource) !== titleKeyResource) {
            params.titleModal = bizagi.localization.getResource(titleKeyResource);
        }

        return params;
    };

    this._messageHtml = function (genericMessage, footerGenericMessage, linkShowDetails, hideDetails, error) {
        return "" +
            "<div><div class='dialog-content-wrapper' style='height:200px;max-height:500px;'>" +
            "<div class='generic-message detailsShown' style='width: 535px; padding-top: 20px;'> " +
            "<div style='overflow:hidden;'>" +
            "<i class='bz-icon bz-icon-error bz-icon-50' " +
            "style='font-size: 32px;float: left;margin: 10px 20px 90px 20px; color: #e55f6f'></i>" +
            "<div style='float:right;width:450px;'>" +

            "<p style='font-size: 21px;' class='genericMessage'>" +
            genericMessage +
            "</p>" +
            "<hr />" +
            "<span class='footerGenericMessage' style='font-size: 12px;'>" + footerGenericMessage + ".<br /> " + linkShowDetails +

            "</span></div>" +
            "</div>" +
            "</div>" +
            "<div class='detailsShown' style='display:none;'>" +
            "<p><span style='font-size: 12px;'><a class='showDetail' href='javascript:void(0)'>" + hideDetails + "</a></span></p>" +
            "<div class='ui-bizagi-error'>" + bizagi.util.encodeHtml(error) + "</div>" +
            "</div>" +
            "</div></div>";
    };

    /**
     * This method shows generic error message
     * @param error
     * @param titleKeyResource
     * @param showGenericMessage
     */
    this._show = function (error, titleKeyResource, showGenericMessage) {
        var params = setDefaultParams(error, titleKeyResource, showGenericMessage);

        if (typeof $ != "undefined" && error && isDesktopDevice) {
            var buttons = {};

            var genericMessage = bizagi.localization.getResource("workportal-general-error-message-contact-administrator");
            var footerGenericMessage = bizagi.localization.getResource("workportal-general-error-message-contact-administrator-footer");
            var showDetails = bizagi.localization.getResource("workportal-general-error-message-view-details");
            var hideDetails = bizagi.localization.getResource("workportal-general-error-message-hide-details");

            var linkShowDetails = "";
            if (bizagiConfig.environment === "debug" || bizagiConfig.isProduction === false) {
                linkShowDetails = "<a class='showDetail' href='javascript:void(0)'>" + showDetails + "</a> ";
            }

            var contentDialog = self._messageHtml(genericMessage, footerGenericMessage, linkShowDetails, hideDetails, error);

            var dialogTemplate = $(contentDialog);
            $("a.showDetail", dialogTemplate).on("click", function () {
                $(".detailsShown", dialogTemplate).toggle();
            });

            if (!params.showGenericMessage) {
                $(".generic-message", dialogTemplate).remove();
                $(".showDetail", dialogTemplate).empty();

                $(".detailsShown", dialogTemplate).show();
                $(".detailsShown", dialogTemplate).addClass("withoutGenericMessage");

            }

            // Hide waiting modal box

            buttons[bizagi.localization.getResource("workportal-case-dialog-box-close")] = function () {
                $(this).dialog('close');
            };

            dialogTemplate.dialog({
                modal: true,
                title: params.titleModal,
                closeOnEscape: true,
                width: 550,
                height: "auto",
                buttons: buttons,
                maximize: false,
                draggable: false,
                close: function () {
                    $(this).dialog('destroy').remove();
                }
            });

            var contentButton = $(".detailsShown.withoutGenericMessage").parent().parent().siblings('div.ui-dialog-buttonpane.ui-widget-content.ui-helper-clearfix');
            $('.ui-button', contentButton).css("margin", "0px");
            $('.ui-dialog-buttonset', contentButton).css("padding", "10px 0px 0px");
        } else {
            if (isDesktopDevice) {
                window.alert(error);
            } else {
                if (bizagi && bizagi.loading) bizagi.loading.stop();

                if (typeof (cordova) !== "undefined") {
                    navigator.notification.alert(error.replace(/(<([^>]+)>)/ig, ""),
                        function () {
                        },
                        "Bizagi",
                        "Ok"
                    );
                } else {
                    // Apply to mobile
                    window.alert(error.replace(/(<([^>]+)>)/ig, ""));
                }
            }
        }
    };

    return {
        show: _show
    }
})();