/**
 * Handle localization user preferences
 * @author: Edward Morales
 */

(bizagi) ? (bizagi.services) ? bizagi.services.preferences = null : bizagi.services = {} : bizagi = {};

bizagi.services.preferences = (function (preferencesWP) {
    var dataService = new bizagi.workportal.services.service();
    var currentTime = new Date();
    preferencesWP=preferencesWP===undefined?{ workportal: {} }:preferencesWP;
    bizagi.currentUser=bizagi.currentUser===undefined?{}:bizagi.currentUser;
    if(!bizagi.currentUser.preferences){
        bizagi.currentUser.preferences = preferencesWP;
    }
    /* 
    * Version variable would be changed when json preferences format change
    */
    var version = 1;

    var _getFormats = function () {
        var storedValues = _getPreferences();
        return {
            language: {
                currentValue: storedValues["language"],
                values: [
                    { value: "", display: "Default" },
                    { value: "es", display: "es" },
                    { value: "es-CO", display: "es-CO" },
                    { value: "en", display: "en" },
                    { value: "en-US", display: "en-US" }
                ]
            },
            longDateFormat: {
                currentValue: storedValues["longDateFormat"],
                values: [
                    { value: "", display: "Default" },
                    { value: "dddd, MMMM dd, yyyy", display: "dddd, MMMM dd, yyyy" },
                    { value: "MMMM dd, yyyy", display: "MMMM dd, yyyy" },
                    { value: "dddd, dd MMMM, yyyy", display: "dddd, dd MMMM, yyyy" },
                    { value: "dd MMMM, yyyy", display: "dd MMMM, yyyy" }
                ]
            },
            shortDateFormat: {
                currentValue: storedValues["shortDateFormat"],
                values: [
                    { value: "", display: "Default" },
                    { value: "M/d/yyyy", display: "M/d/yyyy" },
                    { value: "M/d/yy", display: "M/d/yy" },
                    { value: "MM/dd/yy", display: "MM/dd/yy" },
                    { value: "MM/dd/yyyy", display: "MM/dd/yyyy" },
                    { value: "yy/MM/dd/", display: "yy/MM/dd/" },
                    { value: "yyyy/MM/dd/", display: "yyyy/MM/dd/" },
                    { value: "dd/MM/yyyy", display: "dd/MM/yyyy" },
                    { value: "dd/MMMM/yy", display: "dd/MMMM/yy" },
                    { value: "dd/MM/yy", display: "dd/MM/yy" }
                ]
            },
            dateSeparator: {
                currentValue: storedValues["dateSeparator"],
                values: [
                    { value: "", display: "Default" },
                    { value: "-", display: "-" },
                    { value: "/", display: "/" }
                ]
            },
            timeFormat: {
                currentValue: storedValues["timeFormat"],
                values: [
                    { value: "", display: "Default" },
                    { value: "H.mm", display: "H.mm" },
                    { value: "H:mm", display: "H:mm" },
                    { value: "h:mm tt", display: "h:mm tt" },
                    { value: "h:mm.tt", display: "h:mm.tt" },
                    { value: "H:mm:ss", display: "H:mm:ss" },
                    { value: "h:mm:ss tt", display: "h:mm:ss tt" },
                    { value: "HH.mm", display: "HH.mm" },
                    { value: "HH:mm", display: "HH:mm" },
                    { value: "hh:mm tt", display: "hh:mm tt" },
                    { value: "HH:mm:ss", display: "HH:mm:ss" },
                    { value: "hh:mm:ss tt", display: "hh:mm:ss tt" },
                    { value: "tt h:mm", display: "tt h:mm" },
                    { value: "tt hh:mm", display: "tt hh:mm" }
                ]
            },
            decimalSeparator: {
                currentValue: storedValues["decimalSeparator"],
                values: [
                    { value: "", display: "Default" },
                    { value: ",", display: "," },
                    { value: ".", display: "." },
                    { value: "-", display: "-" },
                    { value: "/", display: "/" }
                ]
            },
            thousandSeparator: {
                currentValue: storedValues["thousandSeparator"],
                values: [
                    { value: "", display: "Default" },
                    { value: ",", display: "," },
                    { value: ".", display: "." },
                    { value: "'", display: "'" }
                ]
            },
            maximizeModals: {
                currentValue: storedValues["maximizeModals"]
            },
            collapseSidebars: {
                currentValue: storedValues["collapseSidebars"]
            },
            goToInbox: {
                currentValue: storedValues["goToInbox"]
            }
        }
    }

    var _getFormatValues = function () {
        return _getFormats();
    }

    var _getFormatValuesByControl = function (control) {
        var formats = _getFormats();
        var values = formats[control] || [];
        return values;
    }


    var _formatTime = function (value) {
        return bizagi.util.dateFormatter.formatDate(currentTime, value);
    }

    var _formatDateSeparator = function (separator) {
        var format = "d" + separator + "M" + separator + "Y";
        return bizagi.util.dateFormatter.formatDate(currentTime, format);
    }

    function _getPreferences() {
        var values = bizagi.currentUser.preferences.workportal;
        return values;
    }

    var _getPreference = function (key) {
        var values = _getPreferences();
        return values[key];
    }

    var _overridePreferences = function (values) {
        var newValues;
        try {
            if (typeof values == "string") {
                newValues = JSON.parse(values);
            } else if (typeof values == "object") {
                newValues = values;
            }
        } catch (e) {
            newValues = values;
        }
        newValues.version = version;
        bizagi.currentUser.preferences.workportal = newValues;
        dataService.setPreferencesWorkportalCurrentUser(bizagi.currentUser.preferences);
        _overrideLocalization();
    }

    var _addPreference = function (key, value) {
        var values = _getPreferences();
        values[key] = value;
        _overridePreferences(values);
    }

   
    var _overrideLocalization = function () {
        var mappedToken = "";
        var values = _getPreferences();
        var numericFormat = bizagi.localization.getResource("numericFormat");
        var localizationMap = {
            //  longDateFormat:"longDateFormat",
            shortDateFormat: "dateFormat",
            //  timeFormat:"timeFormat"
            symbol: ["numericFormat"]
        };

        $.each(values, function (key, value) {

            switch (key) {
                case "groupSeparator":
                    mappedToken = "decimalSymbol";
                    bizagi.localization.setResource(mappedToken, value);
                    break;
                case "language":
                    break;
                case "decimalSeparator":
                    mappedToken = "digitGroupSymbol";
                    bizagi.localization.setResource(mappedToken, value);
                    break;
                case "decimalDigits":
                    break;
                case "symbol":
                    numericFormat[key] = value;
                    bizagi.localization.setResource("numericFormat", numericFormat);
                    break;

                case "longDateFormat":
                case "timeFormat":
                    bizagi.localization.setResource(key, value);
                    break;
                case "shortDateFormat":
                    mappedToken = "dateFormat";
                    bizagi.localization.setResource(mappedToken, value);
                    break;
                case "maximizeModals":
                    mappedToken = "maximizeModals";
                    bizagi.localization.setResource(mappedToken, value);
                    break;
            }

            /* if(localizationMap[key] != ""){
                 mappedToken = localizationMap[key];
                 bizagi.localization.setResource(mappedToken,value);            
             }else if(typeof localizationMap[key] == "object"){
                 mappedToken=localizationMap[key][0];
                 bizagi.localization.setResource(mappedToken,value);
             }*/

        });

    }

    return {
        getFormatValues: _getFormatValues,
        getFormatValuesByControl: _getFormatValuesByControl,
        formatTime: _formatTime,
        formatDateSeparator: _formatDateSeparator,
        getPreferences: _getPreferences,
        getPreference: _getPreference,
        overridePreferences: _overridePreferences,
        addPreference: _addPreference,
        overrideLocalization: _overrideLocalization
    };
});
