(function ($) {

    $.fn.usertooltip = function (bizagiWidget, options) {
        //---------------------------------------------------------
        // Initial config
        //---------------------------------------------------------
        /*
        if (typeof bizagiWidget === 'undefined') {
          throw "Debe pasar como parametro el widget de bizagi, para la carga del template";
        }*/
        var self = this;
        var settings = $.extend({
        }, options);

        var usertooltip = {
        };

        //---------------------------------------------------------
        // Constructor
        //---------------------------------------------------------

        (function () {
            //call to userInformation and it adds a deffer in userinformationDefer
            self.userinformation(bizagiWidget, { user: options.user });
            self.userinformationDefer.done(function($userinformation) {
                var $usertooltip = $userinformation;
                $usertooltip.addClass("userstooltip__container");
                //se renderiza en this, pero el defer retorna el userinformation para agregarle los eventos
                $usertooltip.position({
                    my: 'left top',
                    at: 'left bottom',
                    of: settings.target,
                    collision: 'flip'
                });
                //if the position left is wrong (jquery position error) then, reset the left and top position
                validateFarPosition($usertooltip);
                fadingout(settings.target, $usertooltip);
                fadingout($usertooltip, $usertooltip);
                if (options.closeOn) {
                    options.closeOn.hover(function (params) {
                        closeTooltip($usertooltip)
                    })
                }
            });
        })();

        //---------------------------------------------------------
        // Methods
        //---------------------------------------------------------

        /**
         * add fadeout function to hover element to tooltip
         * @param {Jquery Element} e element to add hover function
         * @param {Jquery Element} tooltip Selector of the tooltip
         */
        function fadingout(e, tooltip) {
            e.hover(function () {
                tooltip.stop(true).fadeTo(400, 1);
            },
                function () {
                    closeTooltip(tooltip)
                });
        }

        /**
         * validate if the tooltip position is farther than its own width
         * @param {Jquery element} $usertooltip self usertooltip
         */
        function validateFarPosition($usertooltip) {
            if (parseInt($usertooltip.css("left").replace("px",""))<-$usertooltip.outerWidth()){
                $usertooltip.css({'left':'','top':''});
            }
        }

        /**
         * adds remove event tooltip for fadeout
         * @param {Jquery Element} tooltip Selector of the tooltip
         */
        function closeTooltip(tooltip) {
            tooltip.fadeOut('400', function () {
                tooltip.remove();
            });
        };

        return usertooltip;
    };
}(jQuery));