(function ($) {

    $.fn.userinformation = function (bizagiWidget, options) {
        //---------------------------------------------------------
        // Initial config
        //---------------------------------------------------------
        /*
        if (typeof bizagiWidget === 'undefined') {
          throw "Debe pasar como parametro el widget de bizagi, para la carga del template";
        }*/
        var self = this;
        var settings = $.extend({
        }, options);

        var userinformation = {
        };

        //---------------------------------------------------------
        // Constructor
        //---------------------------------------------------------

        (function () {
            self.userinformationDefer = new $.Deferred();
            self.initialized = bizagiWidget.loadTemplates({
                "bizagi.userinformation": bizagi.getTemplate("bizagi.userinformation").concat("#ui-bizagi-userinformation"),
                useNewEngine: false
            }).then(function () {
                var params = {
                    user: options.user
                };
                params.user.anonymized=params.user.userAssigned=="00000000-0000-0000-0000-000000000000" || params.user.name=="Unassigned";
                if (params.user.anonymized) {
                    params.user.initials="?";
                }
                self.panelWrapper = bizagiWidget.getTemplate("bizagi.userinformation");
                var $userinformation = $.tmpl(self.panelWrapper, { params: params });
                self.find(".userinformation__container").remove();
                (self.find("svg").length==1?self.parent():self).append($userinformation);
                //if the actual userinformation try to create another break;
                if ($userinformation.parents('.userstooltip__container').length>0 || self.parent().find(".userstooltip__container").length>1) {
                    $userinformation.remove();
                    return false;
                }
                self.find(".userinformation__container").click(function(e) {
                    e.preventDefault();
                    e.stopPropagation();
                });
                self.userinformationDefer.resolve($userinformation);
            });
        })();

        return userinformation;
    };
}(jQuery));